@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@400;500;600;700&display=swap);
@tailwind base;
@tailwind components;
@tailwind utilities;
body, html {
  font-family: 'Public Sans', sans-serif; 
}
body {
   background-color: #FFF9EC;
   height: 100vh;
   background-size: 150px;
}
 body .dark-mode {
  background-color: black;
  margin-bottom: 0px;
  position: absolute;
  height: 100%;
  width: 100%;
} 
h1 {
  font-size: 1.5rem !important;
}
h3 {
  font-size: 1.1rem !important;
}
h2 {
  font-size: 1.2rem !important;
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dark-mode h1 {
  color: white;
}

.my-container {
  max-width: 425px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 10px;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 3%;
  background-color: white;
  border-radius: 15px;
}
@media screen and (max-width: 425px) {
.my-container {
    margin-left: 5rem;
    margin-right: 1rem;
}
}
.dark-mode .my-container {
  background: black;
  margin-bottom: 0px;
}
.notes-list {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
.note.new {
  background-color: #FACA66;
}
.notes-h1 {
  padding-top: 3%;
  text-align: center;
}
textarea {
  border: none;
  resize: none;
  background-color: #FACA66;
}
textarea::-webkit-input-placeholder {
  color: white !important;
}
textarea:-ms-input-placeholder {
  color: white !important;
}
textarea::placeholder {
  color: white !important;
}
textarea:focus {
  outline: none;
}

.toggle-mode {
  margin-top: 10px;
  margin-left: 135px;
  width: 110px !important;
  border-radius: 25px;
  cursor: pointer;
  border-color: black;
  border-style: solid;
  border-width: 2px;
}
.dark-mode .toggle-mode {
  color: white;
  border-color: white;
}
.save-button {
  width: 75px !important;
}

@media screen and (max-width:405px) {
  .toggle-mode {
    margin-left: 105px;
  }
}
.note{
  background-color: #FFE3A8;
  border-radius: 10px;
  padding: 1rem;
  min-height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  white-space: pre-wrap;
}
@media screen and (max-width: 370px) {
  .note {
    width: 237px;
  }
}
@media screen and (max-width: 350px) {
  .note {
    max-width: calc(100% - 59px);
  }
}
.note-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.character-limit {
  color: white;
}
.delete-icon {
  cursor: pointer;
}

.search {
  display: flex;
  align-items: center;
  background-color: rgb(233, 233, 233);
  border-radius: 10px;
  padding: 5px;
  margin-bottom: 1.5em;
}
.search input {
  border: none;
  background-color: rgb(233, 233, 233);
}

.search input:focus {
  outline: none;
}

/* To Do App */
.todo-container {
  max-width: 425px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 10px;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 3%;
  max-height: 100vh;
  background-color: white;
  border-radius: 15px;
}
@media screen and (max-width: 425px) {
  .todo-container {
    margin-left: 80px;
    margin-right: 20px;
  }
}
.dark-mode .todo-container {
  background: black;
}
input {
  margin: 2px 5px;
}
#to-do-form input {
  background-color: #6a83bf;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px auto;
  border-radius: 25px;
  /*filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)); */
  font-size: 1.1em;
  color: white;
}
@media screen and (max-width: 400px) {
  #to-do-form input {
    width: 180px;
}
}

  form#to-do-form {
    margin: 20px auto;
    width: 25rem;
    margin-left: 25px;
  }
  @media screen and (max-width: 420px) {
    form#to-do-form {
      margin-left: 0px;
    }
  }
#to-do-form input::-webkit-input-placeholder {
  color: white !important;
}
#to-do-form input:-ms-input-placeholder {
  color: white !important;
}
#to-do-form input::placeholder {
  color: white !important;
}
/* Removes outline when clicked */
#to-do-form input:focus, #to-do-form button:focus {
  outline: none;
}
@media screen and (max-width: 375px) {
  #to-do-form input {
    margin-left: 0px;
  }
}
#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 25px;
  border: 0;
  background-color: #8DA6E2;
  cursor: pointer;
  color: white;
  /*filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));*/
  font-size: 1.1em;
  margin-left: 25px;
}
@media screen and (min-width: 768px) {
  #to-do-form button {
    width: 100px;
  }
  }
  @media screen and (max-width: 425px) {
    #to-do-form button {
      width: 68px;
      margin-left: 7px;
    }
  }
.list {
  color: black;
  width: 19rem;
  height: 50px;
  margin: 20px auto;
  background-color: #BFCFF7;
  border-radius: 25px;
  /*filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));*/
}
.list .completed {
  text-decoration: line-through;
}
.list ._checked {
opacity: .6;
}

@media screen and (max-width: 425px) {
  .list {
    width: 16rem;
  }
}
.list p {
  padding: 10px;
  position: relative;
  cursor: pointer;
  align-items: center;
}

.list p span {
  position: absolute;
  right: 10px;
  cursor: pointer;
}

.todo-delete {
  margin: 7px 5px;
  font-size: 15px;
  align-items: center;
}

.list p input {
  background-color: transparent;
  border: 0;
  font-size: 1.1em;
  outline: none;
  padding-left: 10px;
}
@media screen and (min-width: 768px) {
  .list p input {
    font-size: 1.2em;
  }
}
.todo-h1 {
  padding-top: 3%;
  text-align: center;
}
input.list__check {
  margin-right: 5px;
}
/*.list__item-checked {
  opacity: 0.6;
}  */


/* Home Page */
.h1-home {
  text-align: center;
}

/* Weather */

.weather-background {
  background: white;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  border-radius: 15px;
  padding-top: 1%;
  padding-bottom: 3%;
  max-width: 425px;
  max-height: 800px;
  margin-top: 10px;
}

@media screen and (max-width: 425px) {
.weather-background {
    margin-left: 5rem;
    margin-right: 1rem;
}
}
.dark-mode .weather-background {
  background-color: black;
}
.weather-container p {
  font-size: 1.1rem !important;
}
.weather-h1 {
  text-align: center;
}
.weather-h2 {
  font-size: 6rem !important;
  color: #7ECECA;
}
.weather-container {
  max-width: 600px;
  max-height: 700px;
  margin: auto;
  position: relative;
  background-color: white;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.top {
  width: 100%;
  margin: 1rem auto;
  text-align: center;
}
.bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  border-radius: 15px;
  background-color: #7ECECA;
}
.description {
  position: relative;
  right: -40%;
  -webkit-transform: rotate(269deg);
          transform: rotate(269deg);
  padding-left: 15%;
}
.bold {
  font-weight: 700;
}
.weather-search {
  text-align: center;
  padding: 1rem;
  background-color: #7ECECA;
  border-radius: 15px;
}
.weather-container input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: none;
  background-color: #d0fcfa;
  text-align: center;
}
@media screen and (max-width: 414px) {
  .weather-container input {
    max-width: 220px;
  }
}
::-webkit-input-placeholder {
  color: black !important;
}
:-ms-input-placeholder {
  color: black !important;
}
::placeholder {
  color: black !important;
}
/* Budget App */

.budget-h1 {
  text-align: center;
  padding-top: 2%;
  font-weight: 600;
}
.grid {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
.budget-section {
  background-color: #f3a19a;
  border-radius: 15px;
  text-align: center;
  padding: 10px;
}
.remaining {
  background-color: #eec3c0;
  border-radius: 15px;
  text-align: center;
  padding: 10px;
}
.expensetotal {
  background-color: #f3a19a;
  border-radius: 15px;
  text-align: center;
  padding: 10px;
}
.expenses {
  text-align: center;
  font-weight: 600;
}
.expenselist {
  list-style-type: none;
  background-color: #eec3c0;
  padding-top: 10px;
  border-radius: 15px;
  padding-bottom: 10px;
}
.expenses-group {
  margin-top: 15px;
}
ul {
  list-style-type: none;
}
.add-expense {
  text-align: center;
  padding-top: 10px;
  background-color: #f3a19a;
  border-radius: 15px;
}
.add-expenses {
  font-weight: 600;
  text-align: center;
}
.add-expenses-group {
  margin-top: 15px;
}
.edit-button {
  width: 60px !important;
  float: right;
  margin-top: -28px;
  margin-right: 5px;
}
.submit-buttonn {
  border-radius: 25px;
  background-color: lightgrey !important;
  width: 60px !important;
  margin-left: 5px;
} 
.submit-button {
  display: inline !important;
  width: 80px !important;
  margin-bottom: 5px;
  margin-top: 5px;
  padding-bottom: 0 !important;
  padding-top: 0 !important;
}
ul {
  list-style-type: none; /* Remove bullets */
  padding: 0; /* Remove padding */
  margin: 0; /* Remove margins */
}

.expense-list li  {
  border: 1px solid #ddd; 
  margin-top: 10px; 
  background-color: white; 
  padding: 15px; 
  border-radius: 15px;
  margin-right: 1rem;
  margin-left: 1rem;

}
.props-name {
  float: left;
  margin-top: -7px;
  line-height: 15px;
}
.props-cost {
  float: right;
  font-size: 1rem;
  line-height: 5px;
}
.budget-delete {
  float: right;
  padding-left: 20px;
  margin-top: -8px;
}
.form {
  margin: 10px 20px;
}
input#cost {
  border-radius: 25px;
  width: 200px;
  height: 30px;
  outline: none;
  padding-left: 5px;
}
input#name {
  border-radius: 25px;
  width: 200px;
  height: 30px;
  outline: none;
  padding-left: 5px;
}
.label-name {
  font-size: 1rem;
}
.label-cost {
  font-size: 1rem;
}
/* Home Page */
.dark-mode .home-container {
  background-color: black;
  height: 100%;
  width: 100%;
}
.dark-mode div.react-grid-layout {
  background-color: black;
}
.home-container {
  margin-left: 80px;
  margin-right: 0;
}
.dark-mode .my-sidebar {
  background-color: black;
}
.dark-mode .sidebar-icon {
  color: white;
}
@media screen and (max-width:425px) {
  .jqNmJQ {
  width: 100vw;
  margin-left: -74px;
  padding: 0 !important;
  overflow: hidden;
}
}
.clock {
  text-align: center;
  width: 200px;
  margin-right: auto;
  margin-left: auto;
  padding: 10px;
  border-radius: 25px;
  font-size: 25px;
  font-weight: 600;
  border-color: black;
  border-width: 3px;
  margin-top: 5px;
}
@tailwind base;
@tailwind components;
@tailwind utilities;


@layer components {
  .sidebar-icon {
    @apply relative flex items-center justify-center
    h-12 w-12 mt-2 mb-2 mx-auto shadow-lg
    hover:bg-black hover:text-white
    rounded-3xl hover:rounded-xl
    transition-all duration-300 ease-linear
    cursor-pointer;
  }
  .submit-button {
    @apply flex shadow w-32 
    border-black border-2 rounded-full 
    focus:outline-none focus:border-black px-4 py-2 
    text-black hover:bg-black hover:text-white;
  }
  .save {
    @apply flex shadow w-32 
    border-black border-2 rounded-full 
    focus:outline-none focus:border-black px-4 py-2 
    text-black hover:bg-black hover:text-white;
  }
  .edit-button {
    @apply flex shadow
    border-black border-2 rounded-full 
    focus:outline-none focus:border-black px-3  
    text-black hover:bg-black hover:text-white;
  }
}
